<template>
  <div class="components-container">
    <!-- <div class="info"></div> -->
    
    <h3 class="title"></h3>
    <div class="main">
          <div class="box">
            <p></p>
            <el-input type="text" placeholder="" v-model="title"></el-input>
          </div>

          <div class="box">
              <p></p>
              <el-date-picker
                v-model="date"
                type="datetime"
                placeholder="">
              </el-date-picker>
          </div>
  
          <div class="editor-container">
            <p></p>
            <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
            <!-- <UE :defaultMsg=defaultMsg :config=config :id=ue2 ref="ue2"></UE> -->
          </div>
           <el-button type="primary" class="pull-right" @click="submit()"></el-button>
    </div>
    
    
  </div>
</template>
<style scoped lang='less'>
.info {
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
}
.editor-container {
  margin-bottom: 20px;
}
.main{
  padding-left: 40px;
  padding-bottom: 40px;
}
.box {
  width: 500px;
  margin: 10px 0 20px;
}

</style>
<script>
import UE from "components/ue/ue.vue";
import { newsAdd } from 'api/news';

export default {
  components: { UE },
  data() {
    return {
      defaultMsg: "",
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350,
      },
      ue1: "ue1", // id
      title: "", //
      content: "", //
      date: "" //
    };
  },
  methods: {
    submit() {
      let content = this.$refs.ue.getUEContent(); // 
      let plainText =  this.$refs.ue.getContentTxt(); //
  
      newsAdd({title:this.title,date:this.date,content,plainText});
    }
  }
};
</script>






